// src/components/GoogleSearch.js
import React, { useEffect } from "react"

const GoogleSearch = () => {
  useEffect(() => {
    if (typeof window === "undefined") return

    const cx = "b4ea278afb57b4ea1" //    CSE ID 

    // 1. Google CSE  
    const renderSearchBox = () => {
      if (window.google && window.google.search && window.google.search.cse) {
        window.google.search.cse.element.render({
          div: "gcse-searchbox",
          tag: "searchbox-only",
          attributes: {
            newWindow: true, //    (  false)
          },
        })
      }
    }

    // 2. window.__gcse 
    window.__gcse = {
      parsetags: "explicit",
      callback: renderSearchBox,
    }

    // 3.  
    if (!document.getElementById("gcse-script")) {
      const script = document.createElement("script")
      script.id = "gcse-script"
      script.type = "text/javascript"
      script.async = true
      script.src = `https://cse.google.com/cse.js?cx=${cx}`
      script.onload = renderSearchBox //  callback    
      document.body.appendChild(script)
    } else {
      //    
      setTimeout(renderSearchBox, 300)
    }
  }, [])

  return <div id="gcse-searchbox" />
}

export default GoogleSearch
